.inside-form-container {
  display: flex;
  flex-direction: column;
}
.inside-form-container > .form-text-input {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form-text-input > label {
  font-weight: 700;
}

.form-fieldset {
  margin: 30px auto;
  box-shadow: 10px 6px 5px rgba(0, 0, 0, 0.548);
  background-color: rgba(240, 248, 255, 0.247);
}

.create-recipe-container {
  width: 80%;
  margin: auto;
}

.submit-recipe-btn {
  width: 50%;
  margin: auto;
  margin-top: 30px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: medium;
  text-align: center;
}

input:invalid {
  border-color: rgba(153, 0, 0, 0.329);
  background-color: #fdd;
}

input:focus:invalid {
  outline: none;
}

input {
  border-radius: 5px;
}

textarea {
  border-radius: 5px;
}

textarea:invalid {
  border-color: rgba(153, 0, 0, 0.329);
  background-color: #fdd;
}

/* input:user-invalid {
  background-color: rgba(240, 16, 16, 0.322);
} */

textarea:focus:invalid {
  outline: non;
}

/* input:valid {
  border-color: rgb(87, 119, 38);
  background-color: greenyellow;
} */

.check-boxes > div {
  background-color: rgba(255, 255, 255, 0.5);
}

.check-boxes {
  display: flex;
  /* border: solid black thin; */
  flex-wrap: wrap;
  justify-content: center;
  max-width: 550px;
  margin: auto;
  /* background-color: rgba(255, 255, 255, 0.116); */
}

.check-boxes > div {
  border: solid thin;
  border-radius: 5px;
  margin: 5px;
  padding-left: 3px;
}

.new-recipe-error-message {
  background-color: rgba(255, 0, 0, 0.301);
  width: fit-content;
  margin: 15px auto;
  padding: 1px 10px;
  border-radius: 10px;
  color: white;
}

.new-recipe-error-message > h4 {
  color: white;
}

.new-recipe-ok-message {
  background-color: rgba(0, 128, 0, 0.301);
  width: fit-content;
  margin: 15px auto;
  padding: 1px 10px;
  border-radius: 10px;
  color: white;
}

.no-diets {
  background-color: rgba(255, 0, 0, 0.301);
  width: fit-content;
  border-radius: 5px;
  margin: 5px auto;
  padding: 5px;
}
